import React from 'react';
import classNames from 'classnames';
import CheckBox from '../CheckBox';
import style from './styles/index.module.scss';
import Icon from '../Icon';
import { Link } from 'react-router-dom';

const Item = ({
  isRead,
  date = '16 дек, 14:15',
  message = 'Проверка соблюдения условий',
  selectItemsNotice = [],
  heandlerCheckNotice,
  product_image_url,
  el,
}) => {

  const { id } = el;

  return (
    <div className={style['cabinet-notifications__item']}>

      <div className={style['cabinet-notifications__item-wrapper']}>
        <CheckBox
          id={id}
          className={style['cabinet-notifications__item-cheked']}
          onChange={(e) => {
            const value = e.id;
            heandlerCheckNotice(value)
          }
          }
          variant="input"
          checked={selectItemsNotice.includes(id)}
        />
        <span
          className={classNames({
            [style['cabinet-notifications__item-mark']]: true,
            [style['cabinet-notifications__item-mark--unread']]: !isRead,
          })}
        ></span>

        {
          product_image_url ?
            <div
              className={style['cabinet-notifications__item-image-wrapper']}
            >
              <Icon
                src={product_image_url}
                width={50}
                height={70}
              />
            </div>
            : null
        }

        <Link
          className={style['cabinet-notifications__item-link-wrapper']}
          to={`/orders/${message.split(' ').reduce( (prev, cur, index, arr) => {
              if(cur.includes('href')){
                  prev = cur.split('/')[2].split('"')[0]
              }
              return prev
            },'')}`
          }
        >

        <span className={style['cabinet-notifications__item-context']} dangerouslySetInnerHTML={{ __html: message.split(' ').reduce( (prev, cur, index, arr) => {
            if(arr[index] === '№'){
                cur = '<strong>№'
            }
            if(arr[index - 2] === '№'){
                cur = arr[index] + '</strong>&nbsp'
            }

            return [ ...prev, cur.replace('/orders/','/')] //.replace('<a','<Link').replace('href','to').replace('</a>','</Link>')
          },[]).join(' ') }}></span>
        </Link>

      </div>
      <span className={style['cabinet-notifications__item-date']}>{date}</span>
    </div>
  );
};

export default React.memo(Item);
